@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
:root{--base-color: #ffc421}*{font-family:'Indie Flower', cursive}h2{font-style:italic;text-decoration:underline}#quote-box{background-color:rgba(145,145,145,0.5);width:50%;height:400px;padding:20px;text-align:center;position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);border-radius:20px}#quote-text{width:100%;height:70%;display:-webkit-flex;display:flex;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}#new-quote{-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-tap-highlight-color:transparent}button{width:250px;height:50px;cursor:pointer;font-size:20px;letter-spacing:5px;background:#ffc421;background:var(--base-color);border:2px solid black;box-shadow:0px 0px 0px rgba(0,0,0,0.4);transition:500ms;padding:0;border-radius:10px;margin-top:5px;margin-bottom:20px}button:after{content:'';position:absolute;-webkit-transform:translateX(-55px) translateY(-40px);transform:translateX(-55px) translateY(-40px);width:25px;height:25px;border-radius:50%;background:transparent;box-shadow:0px 0px 50px transparent;transition:500ms}button:hover{-webkit-transform:translateY(-5px);transform:translateY(-5px);box-shadow:0px 10px 10px rgba(0,0,0,0.4)}button:hover:after{background:white;box-shadow:0px 0px 20px #ffc421,0px 0px 30px #ffc421,inset 0px 0px 10px #ffc421;box-shadow:0px 0px 20px var(--base-color),0px 0px 30px var(--base-color),inset 0px 0px 10px var(--base-color);-webkit-animation:spin 1s infinite linear;animation:spin 1s infinite linear}@-webkit-keyframes spin{25%{-webkit-transform:translateX(-25px) translateY(-35px);transform:translateX(-25px) translateY(-35px);width:15px;height:15px}50%{-webkit-transform:translateX(-55px) translateY(-30px);transform:translateX(-55px) translateY(-30px);width:5px;height:5px}75%{-webkit-transform:translateX(-85px) translateY(-35px);transform:translateX(-85px) translateY(-35px);width:15px;height:15px}}@keyframes spin{25%{-webkit-transform:translateX(-25px) translateY(-35px);transform:translateX(-25px) translateY(-35px);width:15px;height:15px}50%{-webkit-transform:translateX(-55px) translateY(-30px);transform:translateX(-55px) translateY(-30px);width:5px;height:5px}75%{-webkit-transform:translateX(-85px) translateY(-35px);transform:translateX(-85px) translateY(-35px);width:15px;height:15px}}button:focus{outline:none}

